@font-face {
  font-family: "Montserrat";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/montserrat-regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/montserrat-medium.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-weight: 600;
  font-style: normal;
  src: url("../fonts/montserrat-semi-bold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/montserrat-bold.woff2") format("woff2");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011C40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #011C40;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #011C40;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Montserrat", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 50px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.advantages {
  padding: 120px 0;
  background-color: #D8D8D9;
  background-image: url("../img/advantages-background-desktop.jpg");
  min-height: 562px;
  width: auto;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media (min-width: 1024px) and (min-resolution: 2dppx) {
  .advantages {
    background-image: url("../img/advantages-background-desktop-2x.jpg");
    min-height: 562px;
    width: auto;
  }
}
@media (max-width: 1023px) {
  .advantages {
    padding: 100px 0;
    min-height: 588px;
    background-image: url("../img/advantages-background-tablet.jpg");
  }
}
@media (max-width: 1023px) and (min-resolution: 2dppx) {
  .advantages {
    background-image: url("../img/advantages-background-tablet-2x.jpg");
    min-height: 588px;
    width: auto;
  }
}
@media (max-width: 767px) {
  .advantages {
    padding: 65px 0;
    min-height: 997px;
    background-image: url("../img/advantages-background-mobile.jpg");
  }
}
@media (max-width: 767px) and (min-resolution: 2dppx) {
  .advantages {
    background-image: url("../img/advantages-background-mobile-2x.jpg");
    min-height: 997px;
    width: auto;
  }
}

.advantages__title {
  margin: 0;
  margin-bottom: 60px;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  text-align: center;
}
@media (max-width: 1023px) {
  .advantages__title {
    font-size: 26px;
    line-height: 39px;
  }
}
@media (max-width: 767px) {
  .advantages__title {
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 40px;
  }
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
}
@media (max-width: 1023px) {
  .advantages__list {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .advantages__list {
    grid-template-columns: 1fr;
    gap: 30px;
  }
}

.advantages__list-item {
  position: relative;
  background-color: #F9FBFD;
  padding: 30px 30px 50px 30px;
}
@media (max-width: 1023px) {
  .advantages__list-item {
    padding: 26px 15px 25px 20px;
  }
}
@media (max-width: 767px) {
  .advantages__list-item {
    padding: 25px 15px 25px 20px;
  }
}

.advantages__list-item-title {
  margin: 0;
  margin-bottom: 20px;
  padding-top: 57px;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}
.advantages__list-item-title br {
  display: none;
}
@media (max-width: 1023px) {
  .advantages__list-item-title br {
    display: inline;
  }
}
.advantages__list-item-title--ship::before {
  content: "";
  display: block;
  position: absolute;
  top: 30px;
  left: 30px;
  background-image: url("../img/svg/advantages-1.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 27px;
  height: 32px;
}
@media (max-width: 1023px) {
  .advantages__list-item-title--ship::before {
    top: 26px;
    left: 20px;
    width: 21px;
    height: 26px;
  }
}
.advantages__list-item-title--tick::before {
  content: "";
  display: block;
  position: absolute;
  top: 30px;
  left: 30px;
  background-image: url("../img/svg/advantages-2.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 38px;
  height: 32px;
}
@media (max-width: 1023px) {
  .advantages__list-item-title--tick::before {
    top: 26px;
    left: 20px;
    width: 27px;
    height: 26px;
  }
}
.advantages__list-item-title--zodiac::before {
  content: "";
  display: block;
  position: absolute;
  top: 30px;
  left: 30px;
  background-image: url("../img/svg/advantages-3.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 44px;
  height: 32px;
}
@media (max-width: 1023px) {
  .advantages__list-item-title--zodiac::before {
    top: 26px;
    left: 20px;
    width: 36px;
    height: 26px;
  }
}

.advantages__list-item p {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.footer {
  padding: 50px 0;
  background-color: #011C40;
}
@media (max-width: 767px) {
  .footer {
    padding: 40px 0;
  }
}

.footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .footer__wrapper {
    flex-direction: column;
  }
}

.footer__social-network-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  column-gap: 27px;
  row-gap: 10px;
  max-width: 200px;
}
@media (max-width: 1023px) {
  .footer__social-network-list {
    column-gap: 22px;
    max-width: 95px;
  }
}
@media (max-width: 767px) {
  .footer__social-network-list {
    max-width: 100%;
  }
}

.footer__social-network-link {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.footer__social-network-icon {
  color: #F9FBFD;
}
@media (max-width: 1023px) {
  .footer__social-network-icon {
    width: 17px;
    height: 17px;
  }
}

.footer__social-network-link:hover .footer__social-network-icon,
.footer__social-network-link:focus .footer__social-network-icon {
  color: #2c39f2;
}

.footer__social-network-link:active .footer__social-network-icon {
  color: #2c39f2;
  opacity: 0.7;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  padding-top: 35px;
}
@media (max-width: 1023px) {
  .header {
    padding-top: 30px;
  }
}
@media (max-width: 767px) {
  .header {
    padding-top: 20px;
  }
}

.main-block {
  min-height: 650px;
  width: auto;
  background-color: #011C40;
  background-image: url("../img/promo-background-desktop.jpg");
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media (min-width: 1024px) and (min-resolution: 2dppx) {
  .main-block {
    background-image: url("../img/promo-background-desktop-2x.jpg");
    min-height: 650px;
    width: auto;
  }
}
@media (max-width: 1023px) {
  .main-block {
    min-height: 638px;
    background-image: url("../img/promo-background-tablet.jpg");
