.main-block__wrapper {
  padding: 283px 95px 20px 95px;
}
@media (max-width: 1023px) {
  .main-block__wrapper {
    padding: 280px 0 20px 0;
  }
}
@media (max-width: 767px) {
  .main-block__wrapper {
    padding: 185px 25px 20px 0;
  }
}

.main-block__title {
  margin: 0;
  margin-bottom: 30px;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
  color: #F9FBFD;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .main-block__title {
    margin-bottom: 29px;
    font-size: 50px;
    line-height: 61px;
    text-shadow: none;
  }
}
@media (max-width: 767px) {
  .main-block__title {
    margin-bottom: 25px;
    font-size: 36px;
